import QSTabs from '../../../components/QS-tabs/QS-tabs.vue';
import {
	mapState
} from 'vuex'

let _this = this;
let loginResult;
export default {
	components: {
		QSTabs
	},
	data() {
		return {
			title: 'Hello',
			br:'\n',
			tabs: ['动态','作品','文章'],
			current: 0,
			tabsHeight: 0,
			dx: 0,
			nickname:'昵称',
			face:'',
			// isLogin1:false,
			
			
		}
	},
	
	onLoad() {
		
	},
	
	onShow() {
		let userinfo = uni.getStorageSync('userinfo');
		console.log('userinfo:');
		console.log(userinfo);
		
		this.nickname = userinfo.data.nickname;
		this.face = userinfo.data.headPortraitImg;
		
	},
	
	methods: {
		change(index) {
			this.current = index;
		},
		swiperChange({detail: { current }}) {
			this.current = current;
		},
		transition({detail: { dx }}) {
			this.$refs.tabs.setDx(dx);
		},
		animationfinish({detail: { current }}) {
			this.$refs.tabs.setFinishCurrent(current);
		},
		isLogin(){
			var SNAME = uni.getStorageSync('SNAME');
			if(SNAME != ''){
				return true;
			}else{
				return false;
			}
		},
		
		
		
		
		logout: function() {
			uni.showModal({
				title: '提示',
				content: '确定退出登录？',
				confirmColor: '#5677FC',
				success: (res) => {
					if (res.confirm) {
						uni.clearStorage()
						uni.reLaunch({
							url: '/pages/user/login'
						})
					}
				}
			});
		},
		edit() {
			this.tui.toast("功能开发中~")
		},
		tapEvent: function(e) {
			let index = e.currentTarget.dataset.index;
			let url = "";
			if (index == 1) {
				url = '../about/about'
			} else if (index == 2) {
				let key = e.currentTarget.dataset.key;
				url = '../maps/maps?key=' + key




			} else {
				url = '../log/log'
			}
			uni.navigateTo({
				url: url
			})
		},
		github: function(type) {
			if (type == 1) {
				const that = this
				uni.setClipboardData({
					data: 'https://github.com/dingyong0214/ThorUI-uniapp',
					success(res) {
						uni.getClipboardData({
							success(res) {
								that.tui.toast("链接已复制", 2000, true)
							}
						})
					}
				})
			} else {



			}
		},
		previewReward: function() {
			uni.previewImage({
				urls: ["https://thorui.cn/img/reward.jpg"]
			})
		}
	}
}
